/*
 * Inspirations
 * https://www.sarasoueidan.com/blog/focus-indicators/
 * https://css-tricks.com/notes-on-josh-comeaus-custom-css-reset/
 */

/*
 * Custom media queries
 */

@custom-media --dark-mode (prefers-color-scheme: dark);

/*
 * Reset browser defaults
 */
*:where(:not(iframe, canvas, img, svg, video):not(svg *)) {
  all: unset;
  color: revert;
  cursor: revert;
  display: revert;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ol[class],
ul[class] {
  list-style: none;
}

/*
 * a11y
 */

.reader-only:not(:focus):not(:active) {
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
  word-wrap: normal !important;
}

.skip-link {
  position: absolute;
  z-index: 1;

  &:focus {
    --link-color--hover: var(--skip-link-color, var(--oc-gray-9, #212529));
    --focus-ring-color: var(--skip-link-bg, var(--oc-yellow-4, #ffd43b));
    --focus-ring-offset: 0;

    background-color: var(--skip-link-bg, var(--oc-yellow-4, #ffd43b));
    left: 5px;
    padding: 1rem;
    top: 5px;
    z-index: 100000;
  }
}

/*
 * Typography & Colors
 */

:root {
  /* from https://systemfontstack.com */
  --bl-font-sans: ui-sans-serif, -apple-system, BlinkMacSystemFont, avenir next, avenir,
    segoe ui, helvetica neue, helvetica, Ubuntu, roboto, noto, arial, sans-serif;
  --bl-font-serif: ui-serif, Iowan Old Style, Apple Garamond, Baskerville,
    Times New Roman, Droid Serif, Times, Source Serif Pro, serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol;
  --bl-font-monospace: ui-monospace, Menlo, Consolas, Monaco, Liberation Mono,
    Lucida Console, monospace;
  --body-bg: white;
  --body-color: var(--oc-gray-9, black);
  --button-bg--default: var(--oc-gray-4, gray);
  --button-bg--default--hover: var(--oc-gray-2, silver);
  --button-bg--disabled: var(--oc-gray-2, silver);
  --button-bg--primary: var(--theme-primary, var(--button-bg--default));
  --button-bg--primary--hover: var(
    --theme-primary--hover,
    var(--button-bg--default--hover)
  );
  --button-bg--secondary: var(--theme-secondary, var(--button-bg--default));
  --button-bg--secondary--hover: var(
    --theme-secondary--hover,
    var(--button-bg--default--hover)
  );
  --button-bg--pink: var(--oc-pink-9, var(--button-bg--default));
  --button-bg--pink--hover: var(--oc-pink-8, var(--button-bg--default--hover));
  --button-bg--violet: var(--oc-violet-9, var(--button-bg--default));
  --button-bg--violet--hover: var(--oc-violet-8, var(--button-bg--default--hover));
  --button-bg--blue: var(--oc-blue-9, var(--button-bg--default));
  --button-bg--blue--hover: var(--oc-blue-8, var(--button-bg--default--hover));
  --button-bg--teal: var(--oc-teal-9, var(--button-bg--default));
  --button-bg--teal--hover: var(--oc-teal-8, var(--button-bg--default--hover));
  --button-bg--lime: var(--oc-lime-9, var(--button-bg--default));
  --button-bg--lime--hover: var(--oc-lime-8, var(--button-bg--default--hover));
  --button-bg--orange: var(--oc-orange-9, var(--button-bg--default));
  --button-bg--orange--hover: var(--oc-orange-8, var(--button-bg--default--hover));
  --button-bg--red: var(--oc-red-9, var(--button-bg--default));
  --button-bg--red--hover: var(--oc-red-8, var(--button-bg--default--hover));
  --button-bg--grape: var(--oc-grape-9, var(--button-bg--default));
  --button-bg--grape--hover: var(--oc-grape-8, var(--button-bg--default--hover));
  --button-bg--indigo: var(--oc-indigo-9, var(--button-bg--default));
  --button-bg--indigo--hover: var(--oc-indigo-8, var(--button-bg--default--hover));
  --button-bg--cyan: var(--oc-cyan-9, var(--button-bg--default));
  --button-bg--cyan--hover: var(--oc-cyan-8, var(--button-bg--default--hover));
  --button-bg--green: var(--oc-green-9, var(--button-bg--default));
  --button-bg--green--hover: var(--oc-green-8, var(--button-bg--default--hover));
  --button-bg--yellow: var(--oc-yellow-9, var(--button-bg--default));
  --button-bg--yellow--hover: var(--oc-yellow-8, var(--button-bg--default--hover));
  --button-color--default: var(--oc-gray-9, black);
  --button-color--disabled: var(--oc-gray-5, #adb5bd);
  --button-color--primary: var(--button-color--default);
  --button-color--secondary: var(--button-color--default);
  --button-padding-block: 0.5rem;
  --button-padding-inline: 1rem;
  --button-radius: 0.25rem;
  --focus-ring-color: 28 126 214;
  --link-color--hover: var(--oc-blue-6, #228be6);
  --link-color--visited: var(--oc-violet-9, #5f3dc4);
  --link-color: var(--oc-blue-9, #1864ab);
  --selection-bg: var(--oc-yellow-4, initial);
  --selection-color: var(--oc-gray-9, initial);

  color-scheme: light dark;
}

:root[data-theme~="light"] {
  color-scheme: light;
}

:root[data-theme~="dark"] {
  color-scheme: dark;
}

@media (--dark-mode) {
  :root:not([data-theme~="light"]) {
    --body-bg: var(--oc-gray-9);
    --body-color: var(--oc-gray-0, white);
    --link-color: var(--oc-blue-4, #4dabf7);
    --link-color--visited: var(--oc-violet-2, #d0bfff);
    --link-color--hover: var(--oc-blue-2, #a5d8ff);
  }
}

::selection {
  background-color: var(--selection-bg);
  color: var(--selection-color);
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  background-color: var(--body-bg);
  color: var(--body-color);
  font-family: var(--body-font-family, var(--bl-font-sans));
}

a {
  transition-duration: var(--animation-timing);
  transition-property: color;
  transition-timing-function: ease-out;
  will-change: color;

  &:where(:link) {
    color: var(--link-color);
  }

  &:where(:visited) {
    color: var(--link-color--visited);
  }

  &:where(:hover, :active, :focus, :focus-visible) {
    color: var(--link-color--hover);
  }
}

a,
button {
  &:where(:focus, :focus-visible) {
    outline: 2px solid rgb(var(--focus-ring-color));
    outline-offset: var(--focus-ring-offset, 2px);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--headers-color, inherit);
  font-family: var(--headers-font-family, var(--bl-font-sans));
  margin-block-end: var(--spacing);
  margin-block-start: 0;
  margin-inline: auto;
}

p {
  margin-block-end: var(--spacing);
  margin-block-start: 0;
  margin-inline: auto;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

h4 {
  font-size: 1rem;
}

h5 {
  font-size: 0.8125rem;
}

h6 {
  font-size: 0.625rem;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
blockquote {
  line-height: calc(1em + 0.5rem);
  overflow-wrap: break-word;
}

ol:not([class]),
ul:not([class]) {
  margin-inline-start: var(--list-inline-spacing, 1.25rem);

  & li + li {
    margin-block-start: var(--spacing);
  }
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

i,
em {
  font-style: italic;
}

u {
  text-decoration: underline;
}

pre,
code,
kbd,
samp {
  font-family: var(--font-monospace, var(--bl-font-monospace));
  font-size: 1rem;
}

small {
  font-size: 0.8em;
}

sub,
sup {
  font-size: 0.75em;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 1em;
  line-height: 1.15;
}

button {
  cursor: pointer;
}

/*
 * CSS property for animation timings that becomes 0 for folx who don't want animations
 */

:root {
  --animation-timing: 200ms;
}

@media screen and (prefers-reduced-motion: reduce), (update: slow) {
  :root {
    --animation-timing: 0s;
  }
}

/*
 * Set some base styles for elements
 */

body {
  margin: 0;
  min-height: 100vh;
  min-height: 100dvh;
}

img {
  border-style: none;
  max-width: 100%;
}

table {
  border-collapse: collapse;
}

textarea {
  white-space: revert;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template,
[hidden] {
  display: none;
}

/*
 * Layout
 */

:root {
  --spacing: 1rem;
}

.container {
  margin-inline: auto;
  max-width: 1200px;
  padding-inline: var(--spacing);
}

body {
  & > :where(header, main, footer, section) {
    padding-block: var(--spacing);
  }

  & > header {
    align-items: center;

    &.sticky {
      position: sticky;
      top: 0;
      z-index: 10;
    }
  }

  & > footer {
    text-align: center;

    &.sticky {
      position: sticky;
      top: 100%;
      z-index: 10;
    }
  }
}

.grid,
.grid--vertical {
  display: grid;
  gap: var(--grid-spacing, var(--spacing));
}

.grid {
  grid-template-columns: repeat(
    auto-fit,
    minmax(var(--grid-column-min-width, 200px), 1fr)
  );
}

.grid--vertical {
  grid-auto-flow: row;
  grid-auto-rows: var(-grid-row-height, auto);
}

/*
 * Forms
 */

form {
  --input-bg: var(--oc-gray-0, white);
  --input-bg--disabled: var(--oc-gray-2, #e9ecef);
  --input-bg--focus: white;
  --input-border-color: var(--oc-gray-1, #f1f3f5);
  --input-border-color--disabled: var(--oc-gray-2, #e9ecef);
  --input-border-color--focus: var(--oc-gray-3, #dee2e6);
  --input-border-width: 1px;
  --input-padding-block: 1rem;
  --input-padding-inline: 1rem;
  --input-radius: 0.25rem;
  --input-text-color: var(--oc-gray-9, black);
  --input-text-color--focus: var(--oc-gray-9, black);
  --input-text-color--disabled: var(--oc-gray-9, black);
  --input-text-size: 1rem;
  --input-file-button-bg: var(--button-bg--primary, var(--oc-blue-9, blue));
  --input-file-button-color: var(--button-color--primary, white);
  --input-file-button-bg--hover: var(
    --button-bg--primary--hover,
    var(--oc-blue-7, blue)
  );
  --input-file-button-color--hover: var(--button-color--primary--hover, white);
  --input-focus-ring-opacity: 0.5;
  --input-focus-ring: rgb(var(--focus-ring-color) / var(--input-focus-ring-opacity));
  --input-placeholder-color: #6b737b;
  --help-text-opacity: 0.8;
  --required-color: var(--oc-red-9, inherit);
  --select-arrow: url('data:image/svg+xml,%3csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"%3e%3cpath fill="none" stroke="%23343a40" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M2 5l6 6 6-6"/%3e%3c/svg%3e');

  & fieldset {
    & legend {
      font-weight: bolder;
      margin-block-end: var(--spacing);
    }
  }

  & fieldset + fieldset {
    margin-block-start: calc(var(--spacing) * 2);
  }

  & label {
    display: block;
    margin-block-end: 0.25rem;
  }

  & .help {
    display: block;
    font-style: italic;
    margin-block-start: 0.25rem;
    opacity: var(--help-text-opacity);
  }

  & abbr[title~="required" i] {
    color: var(--required-color);
    text-decoration: none;
  }

  & :where(input[type]:not([type="radio"]):not([type="checkbox"]), textarea, select) {
    background-clip: padding-box;
    background: var(--input-bg);
    border: var(--input-border-width) solid var(--input-border-color);
    border-radius: var(--input-radius);
    color: var(--input-text-color);
    display: block;
    font-size: var(--input-text-size);
    min-height: 56px;
    padding-block: var(--input-padding-block);
    padding-inline: var(--input-padding-inline);
    transition-duration: var(--animation-timing);
    transition-property: box-shadow, background-color, border-color;
    transition-timing-function: ease-out;
    width: 100%;
    will-change: box-shadow, background-color, border-color;

    &:where(:focus, :focus-visible) {
      background-color: var(--input-bg--focus);
      border-color: var(--input-border-color--focus);
      box-shadow: 0 0 0 4px var(--input-focus-ring);
      color: var(--input-text-color--focus);
      outline: none;
    }

    &:where([disabled], [readonly]) {
      border-color: var(--input-border-color--disabled);
      background-color: var(--input-bg--disabled);
      color: var(--input-text-color--disabled);
      opacity: 0.8;
    }

    &:where([disabled]) {
      cursor: not-allowed;
    }

    &::placeholder {
      color: var(--input-placeholder-color);
    }
  }

  & input[type="number"].no-arrows {
    -moz-appearance: textfield;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  & input[type="file"] {
    overflow: hidden;

    &::file-selector-button,
    &::-webkit-file-upload-button {
      align-items: center;
      background: var(--input-file-button-bg);
      border: 2px solid transparent;
      color: var(--input-file-button-color);
      display: inline-flex;
      filter: brightness(1);
      font-weight: normal;
      justify-content: center;
      margin-block: -1rem;
      margin-inline-start: -1rem;
      margin-inline-end: var(--spacing);
      min-height: 100%;
      padding: 1rem;
      text-align: center;
      transition-duration: var(--animation-timing);
      transition-property: background-color;
      will-change: background-color;
    }

    &:not(:disabled):not([readonly]) {
      &::file-selector-button,
      &::-webkit-file-upload-button {
        cursor: pointer;
      }
    }

    &:where(:hover, :focus, :focus-visible) {
      &::file-selector-button,
      &::-webkit-file-upload-button {
        --input-file-button-bg: var(--input-file-button-bg--hover);
        --input-file-button-color: var(--input-file-button-color--hover);
      }
    }
  }

  & select {
    -webkit-appearance: none;
    appearance: none;
    padding-block: var(--input-padding-block);
    padding-inline: var(--input-padding-inline);
    word-wrap: normal;

    &:where(:not([multiple])) {
      background-image: var(--select-arrow);
      background-position: right 0.75rem center;
      background-repeat: no-repeat;
      background-size: 16px 12px;
      padding-inline-end: calc(1.75rem + var(--input-padding-inline));
    }

    &:where([multiple]) {
      overflow-x: hidden;
      overflow-y: scroll;

      & option {
        align-items: center;
        display: flex;
        margin: 0.25rem -0.5rem;
        min-height: 1.5em;
        padding: 0 0.5rem;
      }
    }
  }
}

:root[dir="rtl"] {
  & form {
    & select {
      background-position: left 0.75rem center;
    }
  }
}

@media (--dark-mode) {
  :root:not([data-theme~="light"]) {
    & form {
      --input-bg: var(--oc-gray-7, #495057);
      --input-bg--disabled: var(--oc-gray-8, #343a40);
      --input-bg--focus: var(--oc-gray-7, #495057);
      --input-border-color: var(--oc-gray-8, #343a40);
      --input-border-color--disabled: var(--oc-gray-8, #343a40);
      --input-border-color--focus: var(--oc-gray-7, #495057);
      --input-focus-ring-opacity: 1;
      --input-placeholder-color: var(--oc-gray-1, #f1f3f5);
      --input-text-color: var(--oc-gray-0, white);
      --input-text-color--disabled: var(--oc-gray-4, #ced4da);
      --input-text-color--focus: var(--oc-gray-0, white);
      --required-color: var(--oc-red-4, inherit);
      --select-arrow: url('data:image/svg+xml,%3csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"%3e%3cpath fill="none" stroke="%23ffffff" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M2 5l6 6 6-6"/%3e%3c/svg%3e');
    }
  }
}

/*
 * Buttons
 */

:where(button, .button) {
  --button-bg: var(--button-bg--default);
  --button-color: var(--button-color--default);

  align-items: center;
  background: var(--button-bg);
  border: 2px solid transparent;
  border-radius: var(--button-radius);
  color: var(--button-color);
  display: inline-flex;
  font-weight: bold;
  justify-content: center;
  min-height: 42px;
  padding: var(--button-padding-block) var(--button-padding-inline);
  text-align: center;
  text-decoration: none;
  transition-duration: var(--animation-timing);
  transition-property: color, background-color;
  will-change: color, background-color;

  &:where(:hover, :active, :focus, :focus-visible) {
    --button-bg: var(--button-bg--default--hover);
    --button-color: var(--button-color--default--hover, var(--button-color--default));
  }

  &.no-bg {
    background-color: transparent;
  }

  &.primary {
    --button-bg: var(--button-bg--primary);
    --button-color: var(--button-color--primary);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--primary--hover, var(--button-bg--primary));
      --button-color: var(--button-color--primary--hover, var(--button-color--primary));
    }
  }

  &.secondary {
    --button-bg: var(--button-bg--secondary);
    --button-color: var(--button-color--secondary);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--secondary--hover, var(--button-bg--secondary));
      --button-color: var(
        --button-color--secondary--hover,
        var(--button-color--secondary)
      );
    }
  }

  &.pink {
    --button-bg: var(--button-bg--pink);
    --button-color: var(--button-color--pink);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--pink--hover, var(--button-bg--pink));
      --button-color: var(--button-color--pink--hover, var(--button-color--pink));
    }
  }

  &.violet {
    --button-bg: var(--button-bg--violet);
    --button-color: var(--button-color--violet);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--violet--hover, var(--button-bg--violet));
      --button-color: var(--button-color--violet--hover, var(--button-color--violet));
    }
  }

  &.blue {
    --button-bg: var(--button-bg--blue);
    --button-color: var(--button-color--blue);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--blue--hover, var(--button-bg--blue));
      --button-color: var(--button-color--blue--hover, var(--button-color--blue));
    }
  }

  &.teal {
    --button-bg: var(--button-bg--teal);
    --button-color: var(--button-color--teal);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--teal--hover, var(--button-bg--teal));
      --button-color: var(--button-color--teal--hover, var(--button-color--teal));
    }
  }

  &.lime {
    --button-bg: var(--button-bg--lime);
    --button-color: var(--button-color--lime);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--lime--hover, var(--button-bg--lime));
      --button-color: var(--button-color--lime--hover, var(--button-color--lime));
    }
  }

  &.orange {
    --button-bg: var(--button-bg--orange);
    --button-color: var(--button-color--orange);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--orange--hover, var(--button-bg--orange));
      --button-color: var(--button-color--orange--hover, var(--button-color--orange));
    }
  }

  &.red {
    --button-bg: var(--button-bg--red);
    --button-color: var(--button-color--red);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--red--hover, var(--button-bg--red));
      --button-color: var(--button-color--red--hover, var(--button-color--red));
    }
  }

  &.grape {
    --button-bg: var(--button-bg--grape);
    --button-color: var(--button-color--grape);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--grape--hover, var(--button-bg--grape));
      --button-color: var(--button-color--grape--hover, var(--button-color--grape));
    }
  }

  &.indigo {
    --button-bg: var(--button-bg--indigo);
    --button-color: var(--button-color--indigo);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--indigo--hover, var(--button-bg--indigo));
      --button-color: var(--button-color--indigo--hover, var(--button-color--indigo));
    }
  }

  &.cyan {
    --button-bg: var(--button-bg--cyan);
    --button-color: var(--button-color--cyan);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--cyan--hover, var(--button-bg--cyan));
      --button-color: var(--button-color--cyan--hover, var(--button-color--cyan));
    }
  }

  &.green {
    --button-bg: var(--button-bg--green);
    --button-color: var(--button-color--green);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--green--hover, var(--button-bg--green));
      --button-color: var(--button-color--green--hover, var(--button-color--green));
    }
  }

  &.yellow {
    --button-bg: var(--button-bg--yellow);
    --button-color: var(--button-color--yellow);

    &:where(:hover, :active, :focus, :focus-visible) {
      --button-bg: var(--button-bg--yellow--hover, var(--button-bg--yellow));
      --button-color: var(--button-color--yellow--hover, var(--button-color--yellow));
    }
  }

  &:where(:disabled, [disabled]) {
    --button-bg: var(--button-bg--disabled);
    --button-color: var(--button-color--disabled);

    border-color: var(--button-bg);
    cursor: not-allowed;
    pointer-events: none;
  }
}

form {
  & :where(button, .button) {
    min-height: 56px;
  }
}

